<template>
  <div id="app">
    <div class="header">
        資料類型 : 
        <select id="selectData" @change="changeData()" :disabled="gameStatus">
            <option value="food" selected>食物</option>
            <option value="drink">飲料</option>
            <option value="truefalse">是非選擇</option>
            <option value="custom">自訂選項</option>
        </select>
        轉盤類型 : 
        <select id="selectTpyeData" @change="changeTypeData()" :disabled="gameStatus">
            <option value="type1" selected>指針轉</option>
            <option value="type2">轉盤轉</option>
        </select>
    </div>
    
    <div class="lottery-sector" id="lottery-sector">
        <div id="bgRotate" class="bgRotate" @click="go()">
            <div class="lottery-sector-color" id="lottery-sector-color">
              <span v-for="(key,index) in data" :key="index" :style="getSpanStyle(index)"></span>
            </div>
            <div class="lottery-gift" id="lottery-gift">
              <span v-for="(key,index) in data" :key="index" :style="'transform: rotate('+index * 360 / data.length+'deg)'"><div>{{key.text}}</div></span>
            </div>
        </div>
        
        <div id="triangle" @click="go()">
            <div id="triangle-up">

            </div>
        </div>
    </div>
    <div id="output">
        今天我想來點<br>
        <div id="outputText"></div>

        --------------
        <br>
        遊戲規則:<br>
        選到什麼就做什麼

    </div>
    <div id="dataSetting">
      <div v-for="(key,index) in data" :key="index">
        {{index+1}} . <input type="text" :id="'input_'+index" :value="key.text" @keyup="updateData(index)"> <button @click="deleteData(index)">remove</button>
      </div>
      <div>
        + . <input type="text" id="addText"> <button @click="addData()">add</button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  components: {
    
  },
  data(){
    return{
      dataType : 1,
      selectData : [
        [
          { text : '花月嵐拉麵' },
          { text : '九湯屋拉麵' },
          { text : '山東水餃王' },
          { text : '高勁牛肉麵' },
          { text : '爭鮮壽司' },
          { text : '定食 8' },
          { text : '大心' },
          { text : '樂昀牛排' },
          { text : '珍宴館' },
          { text : '好享宴小館' },
          { text : '台南虱目魚' },
          { text : '帕米義麵屋' },
          { text : '麵工坊' },
          { text : '吉購吉' },
          { text : '韓國料理' },
          { text : '秋葵犬' },
          { text : '一條通' },
          { text : '龍涎居' },
          { text : '咕咕G廚房' },
          { text : '不會開早餐店' },
          { text : '大埔鐵板燒' },
          { text : '一鼎鮮肉羹' },
          { text : '牛肉蓋飯' },
          { text : '薩利亞' },
          { text : '麥當勞' },
          { text : '漢堡王' },
          { text : '角屋鍋燒' },
          { text : '潛艇堡' },
          { text : '越南菜' },
          { text : '客家菜' },
          { text : '八方雲集' },
          { text : '茱蒂漢堡' },
          { text : '野菇屋比薩'},
          { text : '旺達熱炒'}
        ],
        [
          { text : '50 嵐' },
          { text : '麻古茶坊' },
          { text : '水巷茶弄' },
          { text : '乾杯' },
          { text : '茶湯會' },
          { text : '珍煮丹' },
          { text : '不要對我尖叫' },
          { text : '台茶1號' },
          { text : '清心福全' },
          { text : '這咖啡'}
        ],
        [
          { text : '是' },
          { text : '否' },
        ],
        [
          { text : '自訂1' },
          { text : '自訂2' },
          { text : '自訂3' },
        ]
      ],
      data : [],
      dataIndex : 0,
      gameStatus : false,
      oldRotate : 0,
      RotateTime : 1800,
    }
  },
  mounted: function(){
    this.data = this.selectData[0];
    this.initData();
  },
  methods: {
    getSpanStyle(key){
      var styleText = '';
      if(this.data.length == 1){
          styleText = 'width:100%;height:100%;left:0;top:0;transform-origin: 50% 50%;transform: rotate(0deg) skew(0deg);';
      }else if(this.data.length == 2){
          styleText = `width:100%;height:50%;left:0;top:0;transform-origin: 50% 100%;transform: rotate(${key * 360 / this.data.length - 0}deg) skew(0deg);`;
      }else{
          styleText = `transform: rotate(${key * 360 / this.data.length - ((180 / this.data.length) - (-90 +  360 / this.data.length)) }deg) skew(${-90 +  360 / this.data.length}deg);`;
      }

      if(this.data.length%2 != 0){
        if(key == 0) styleText = styleText + 'background-color: #999;'
      }
      return styleText;
    },
    initData(){
      // 隨機順序
      this.data.sort(function(){return Math.random()>0.5?-1:1;});
    },
    addData(){
      var val = document.getElementById('addText').value;
      var _data = {
        text : val
      }
      this.data.push(_data);
      if(this.dataIndex == 3){
        this.selectData[3] = this.data;
      }
      document.getElementById('addText').value = '';
    },
    updateData(index){
      var val = document.getElementById('input_'+index).value;
      this.data[index].text = val;
      if(this.dataIndex == 3){
        this.selectData[3] = this.data;
      }
    },
    deleteData(index){
      this.data.splice(index,1);
      if(this.dataIndex == 3){
        this.selectData[3] = this.data;
      }
    },
    changeData(){
      var val = document.getElementById('selectData').value;
      switch(val){  
          case 'food': this.data = this.selectData[0]; this.dataIndex = 0; break;
          case 'drink': this.data = this.selectData[1]; this.dataIndex = 1; break;
          case 'truefalse': this.data = this.selectData[2]; this.dataIndex = 2; break;
          case 'custom': this.data = this.selectData[3]; this.dataIndex = 3; break;
          default: this.data = this.selectData[0]; this.dataIndex = 0;
      }
      this.initData();
    },

    changeTypeData(){
      var val = document.getElementById('selectTpyeData').value;
      switch(val){  
          case 'type1': this.dataType = 1; break;
          case 'type2': this.dataType = -1; break;
          default: this.dataType = 1;
      }
      document.getElementById('triangle').style.transform = 'rotate(0deg)';
      document.getElementById('bgRotate').style.transform = 'rotate(0deg)';
      this.oldRotate = 0;
    },
    go(){
      var _this = this;
      if(_this.gameStatus == false){
        _this.gameStatus = true;
        var btn;
        if(_this.dataType == 1){
            btn = document.getElementById('triangle');
        }else{
            btn = document.getElementById('bgRotate');
        }
        if(_this.oldRotate > _this.RotateTime){
            _this.oldRotate = _this.oldRotate - _this.RotateTime;
        }
        btn.style.transform = 'rotate('+_this.dataType * _this.oldRotate+'deg)';

        var style = document.createElement('style');
        style.type = 'text/css';
        var random = (Math.floor(Math.random()*360)+ 0);
        var rotate =  parseInt(random + _this.RotateTime);

        var keyFrames = `
            @keyframes zhuanzhuanzhuan{
                0%{
                    transform: rotate(${_this.dataType * _this.oldRotate}deg);
                } 
                100%{
                    transform: rotate(${_this.dataType * rotate}deg); 
                }
            }
        `;
        style.innerHTML = keyFrames;
        document.getElementsByTagName('head')[0].appendChild(style);
        btn.style.animation = 'zhuanzhuanzhuan 3.5s forwards ease-in-out';
        setTimeout(function(){
            btn.style.animation = '';
            _this.oldRotate = rotate;
            btn.style.transform = 'rotate('+_this.dataType * rotate+'deg)';
            document.getElementsByTagName('head')[0].removeChild(style);
            var id = Math.round((random  / (360 / _this.data.length)));
            if(id == _this.data.length){
                id = 0;
            }
            document.getElementById('outputText').innerText = _this.data[id].text;
            _this.gameStatus = false;
        },3500);

      }
    }
  }
}
</script>

<style>
body{
    margin: 0 auto;
    background: #fff;
    text-align: center;
}

select{
    position: relative;
    display: inline-block;
    margin: 30px auto;
    width: 100px;
    height: 30px;
    font-size: 16px;
    border: 1px solid #ccc;
}

#dataSetting{
  text-align: left;
  width: 360px;
  position: relative;
  margin: 0 auto;
}

#dataSetting div{
  padding: 5px;
  border: 1px solid #ccc;
  border-bottom: 0;
}

#dataSetting div:nth-last-child(1){
  border-bottom: 1px solid #ccc;
}

