    width: 360px;
    height: 360px;
    position: relative;
    margin: 0px auto 30px auto;
    overflow: hidden;
    top: 0;
    border-radius: 50%;
    border: 10px solid #fff;
    box-shadow: 0px 0px 10px #000;
}

