    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    left: 0;
    border-radius: 50%;
    overflow: hidden;
}


.lottery-sector-color{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    left: 0;
}

.lottery-sector>.bgRotate>.lottery-sector-color>span {
    position: absolute;
    left: 50%;
