    width: 100%;
    height: 50%;
    transform-origin: 0% 100%;
    background-color: #ccc;
    /* border: 2px solid #fff; */
}

.lottery-sector>.bgRotate>.lottery-sector-color>span:nth-child(2n){
    /* background-color: #b0e0e6 */
    /* background-color: #8d1f1f */
    background-color: #ec7220
}

.lottery-sector>.bgRotate>.lottery-sector-color>span:nth-child(2n-1){
    /* background-color: #e4c6d0 */
